body {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}
body {
  background-image: url("https://bit.ly/webApp_Assets_background");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: sans-serif;
  font-size: 24px;
  color: white;
  font-weight: 100;
}

.weather-wrapper {
  display: grid;
  grid-template-rows: repeat(auto-fit, minmax(80px, auto));
}

p {
  text-align: center;
}

.main-icons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.five-day-forecast {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.current-temp {
  margin: 0.2em;
  font-size: 2em;
}

.range {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-column-gap: 1em;
  margin: 0.5em;
}

.small-image {
  object-fit: contain;
  margin: auto;
  height: 5em;
  width: 5em;
}

.low-temp {
  justify-self: end;
  margin-right: 1em;
}

.high-temp {
  justify-self: start;
}

h1,
h2 {
  text-align: center;
}

.arrow-image {
  height: 50px;
  width: 50px;
  margin: 5px;
}

.big-image {
  object-fit: contain;
  height: 7em;
  width: 7em;
}

.weather-condition {
  font-size: 1.5em;
  margin: 0.5em;
}

hr {
  width: 100%;
  height: 0;
  margin-left: auto;
  margin-right: auto;
}

.day {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.lds-dual-ring {
  -webkit-align-self: center;
          align-self: center;
  justify-self: center;
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #fff;
  border-color: #fff transparent #fff transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.today-temp {
  text-align: center;
}

.city-title-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.city-title {
  font-weight: lighter;
  font-size: 1.5em;
}

.loader-wrapper {
  display: grid;
  height: 100vh;
}

